/* width */
::-webkit-scrollbar {
  width: 6px;
}
/* Track */
::-webkit-scrollbar-track {
  background: #323232;
}
/* Handle */
::-webkit-scrollbar-thumb {
  background: #0d7377;
  border-radius: 10px;
}
/* Handle on hover */
